import React, { useContext } from 'react';
import styled from 'styled-components';
import {bool} from 'prop-types';
import VisuallyHidden from './VisuallyHidden';
import { IconMoon, IconSun } from './Icons';
import { StateContext, DispatchContext } from '../../theme';

const StyledLabel = styled.label`
  display: ${p => p.desktop ? 'flex' : 'none'};
  padding-left: ${p => p.desktop ? '1.125rem' : '0'};

  &:hover {
    cursor: pointer
  }
  
  ${VisuallyHidden}:focus + * {
    outline: ${props => props.theme.accentColor} 2px dotted;
  }

  @media ${({theme}) => theme.medium} {
    display: ${p => p.desktop ? 'none' : 'flex'};
  }
`;

const DarkModeToggler = ({ desktop }) => {
  const state = useContext(StateContext);
  const dispatch = useContext(DispatchContext);
  return (
    <StyledLabel desktop={desktop}>
      <VisuallyHidden
        type='checkbox'
        onChange={() => {
          dispatch({type: 'TOGGLE_THEME'});
          // window.localStorage.setItem('theme', state.theme);
        }}
        checked={state.theme === 'dark'}
      />
      <IconSun hidden={state.theme !== 'dark'} />
      <IconMoon hidden={state.theme === 'dark'} />
    </StyledLabel>
  );
};

DarkModeToggler.propTypes = {
  desktop: bool
};

export default DarkModeToggler;
